import React, {useEffect,useState} from 'react';
import './style/App.css';
import Recipe from './components/Recipe';


const App=()=> {
  const AppId = "1bbe4936";
  const AppKey= "1cfa492a65d64fc3eeaf4ab805f26080";

  const [recipes,setRecipes]= useState([]);
  const [search,setSearch]= useState("");
  const [query,setQuery] = useState('chicken');
  
  //console.log(recipes);
  useEffect( () => {
    getRecipes();
    
  },[query]);

  
  const getRecipes = async ()=>{
    const response = await fetch(`https://api.edamam.com/search?q=${query}&app_id=${AppId}&app_key=${AppKey}`);
    const data = await response.json();
    setRecipes(data.hits);
    
  }

  const updateSearch= e =>{
    setSearch(e.target.value)
  }
  const getSearch = e =>{
    e.preventDefault()
    setQuery(search)
    setSearch("")
  }
  return (
    <div className="App">
     <form onSubmit={getSearch} className="search-form">
       <input placeholder="search" className="search-bar" type="text" value={search} onChange={updateSearch}/>
       <button className="search-button" type="submit">Search</button>
     </form>
     <div className="recipes">
     {recipes.map(recipe=>(
       <Recipe 
       key={recipe.recipe.label}
       title={recipe.recipe.label}
       calories={recipe.recipe.calories}
       image={recipe.recipe.image}
       ingredients={recipe.recipe.ingredients}
       />
     ))}
     </div>
    
    </div>
  );
}

export default App;
